import React, { useState } from 'react';
import * as Yup from 'yup';
import { Link } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useDispatch } from 'react-redux';

// material-ui
import { makeStyles } from '@material-ui/styles';
import { Button, IconButton, InputAdornment, Stack, Typography } from '@material-ui/core';

// assets
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { useAuth } from '../../../hooks/useAuth';
import { FormProvider, RHFCheckbox, RHFTextField } from '../../../components/hook-form';

// style constant
const useStyles = makeStyles((theme) => ({
    loginInput: {
        ...theme.typography.customInput
    }
}));

//============================|| LOGIN ||============================//

const RestLogin = (props, { ...others }) => {
    const classes = useStyles();
    const dispatcher = useDispatch();
    const [isChecked, setIsChecked] = useState(true);
    const [showPassword, setShowPassword] = useState(false);
    const auth = useAuth();

    const LoginSchema = Yup.object().shape({
        email: Yup.string().email('Email must be a valid email address').required('Email is required'),
        password: Yup.string().required('Password is required')
    });

    const defaultValues = {
        email: '',
        password: '',
        remember: true
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(LoginSchema),
        defaultValues
    });

    const {
        reset,
        setError,
        handleSubmit,
        formState: { errors, isSubmitting }
    } = methods;
    const onSubmit = (data) => {
        const payload = {
            username: data?.email,
            password: data?.password
        };
        auth.signin(
            payload,
            () => {},
            () => {}
        );
    };

    return (
        <>
            <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                <Stack className={classes.loginInput}>
                    <RHFTextField name="email" placeholder="Email address" className={classes.loginInput} autoFocus />
                    <RHFTextField
                        className={classes.loginInput}
                        name="password"
                        placeholder="Password"
                        type={showPassword ? 'text' : 'password'}
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    <IconButton onClick={() => setShowPassword(!showPassword)} edge="end">
                                        {showPassword ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            )
                        }}
                        required
                    />
                </Stack>
                <Stack direction="row" alignItems="center" justifyContent="space-between" spacing={1}>
                    {/* <RHFCheckbox name="remember" label="Remember me" /> */}
                    <Typography variant="subtitle1" component={Link} to="/forgot-password" color="primary" sx={{ textDecoration: 'none' }}>
                        Forgot Password?
                    </Typography>
                </Stack>

                <Button disableElevation disabled={isSubmitting} fullWidth size="large" type="submit" variant="contained" color="primary">
                    LOG IN
                </Button>
            </FormProvider>
        </>
    );
};

export default RestLogin;
